import { createSlice, PayloadAction } from '@reduxjs/toolkit'
//import { nanoid } from 'nanoid'

export const initialState = {
  service: 
    {
      name: '',
      desc: 'Description',
    }
}


//  
// Reducer    ("slicename")    . -> 'slicename/increment', 'slicename/decrement'
//          .
const slice = createSlice({
  name: 'serviceslice',  // Slice 
  initialState: initialState,
  reducers: {
    increment: (state) => {
      state.service.name = "A";
      state.service.desc = " ";
    },
    decrement: (state) => {
        state.service.name = "B";
        state.service.desc = " B";
    },
    setservicename: (state, action) => {
        state.service.name = action.payload;    
    }
  },
});

// Action  export
export const { increment, decrement } = slice.actions;

export default slice.reducer; 